@import url(https://fonts.googleapis.com/css?family=Inconsolata&display=swap);
title {
    font-size: 0rem;
}
   
.LocationEntry {
    font-family: 'Inconsolata', sans-serif;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 3rem;
    margin-bottom: .5rem;
    margin-left: 5rem;
    margin-right: 5rem;
    padding-left: 5rem;
    padding-right: 5rem;
}

h3 {
    text-align: center;
    font-family: 'Inconsolata', sans-serif;
}

h4 {
    margin-bottom: 0rem;
    color: #3C1D09; 
    font-family: 'Inconsolata', sans-serif;
}

p {
    text-align: center;
    font-family: 'Inconsolata', sans-serif;
    color:#3C1D09; 
    margin-bottom: 1rem;
