    margin-right: 3rem;
    padding-left: 3rem;
    padding-right: 3rem;
}

h1 {
    color: #3C1D09;
    text-align: center;
    margin-bottom: 2rem;
    margin-top: 3rem;
} 

h2 {
    text-align: center;
}

h6 {
